<template>
	<div>
		<hello title="个人中心"></hello>
    	<pag></pag>
		<div class="login_div">
			<input type="text" placeholder="账户名/手机号" class="login_text">
		</div>
		<div class="login_div">
			<input type="password" placeholder="请输入您的密码" class="login_text">
		</div>
		<div>
			<input type="submit" value="登陆" class="login" @touchstart="logint">
		</div>
		<div class="login_a">
			<a href="#" @touchstart="logint">立即注册</a>
			<a href="#" @touchstart="logint">修改密码</a>
		</div>	
		<p v-if="istrue" class="pshow">此项功能暂时没有</p>
	</div>
</template>
<script>
	import hello from "@/components/HelloWorld"
  	import pag from "@/components/pag1"
	export default{
		name:"stou",
		data(){
			return{
				istrue:false
			}
		},
		components:{hello,pag},
		methods:{
			logint(){
				this.istrue = true;
				setTimeout(()=>{
					this.istrue=false;
				},1500)
			}
		}
	}
</script>
<style scoped>
	.login_text{
		width: 100%;
		height: 40px;
		margin-top: 5px;
		/*input自带1pxpadding和2px宽度的border*/
		box-sizing: border-box;
		border: none;
		padding-left: 10px;
		font-size: 16px;
	}
	.pshow{width: 100%;font-size: 20px;font-weight: bold;color: #aaa;margin-top: 30px;text-align: center;}
	.login_div{
		width: 100%;
		overflow: hidden;
		margin-top: 10px;
		border-bottom: 1px solid #eee;
	}
	.login_a{
		display: flex;
		justify-content: space-between;
	}
	.login{
		width: 100%;
		height: 40px;
		background: #e54847;
		border: none;
		border-radius: 5px;
		color: white;
		font-size: 16px;
		font-weight: bold;
		margin-top: 15px;
		margin-bottom: 10px;
	}
	a{
		text-decoration: none;
		color: #e54847;
		font-size: 12px;
	}
</style>


